.button {
  height: 64px;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  animation: anim 5s ease-in-out forwards;
  width: 146px;
}
@keyframes anim {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.4;
    animation-timing-function: ease-in-out;
  }
  100% {
    opacity: 1;
    animation-timing-function: ease-in-out;
  }
}
